body {
    overflow: hidden;
}

.index_container {
    height: 100vh;
    text-align: center;
    background-size: auto auto;
    background-color: #F5F5DC;
    background-image: repeating-linear-gradient(135deg, transparent, transparent 15px, rgba(0, 0, 0, 0.1) 15px, rgba(0, 0, 0, 0.1) 16px), repeating-linear-gradient(45deg, transparent, transparent 15px, rgba(0, 0, 0, 0.1) 15px, rgba(0, 0, 0, 0.1) 16px);
}

.acordion_checkbox {
    display: none;
}

.acordion_wrapper {
    padding: 1rem;
}

.index_info_label {
    border: black solid 0.1rem;
    border-radius: 0.5rem;
    color: black;
    padding: 0.25rem 0.5rem;
    margin: 4rem 0px;
    background-color: pink;
}

.index_info_label:hover {
    color: rgba(0, 0, 0, 0.5);
    background-color: whitesmoke;
}

.index_info_label:active {
    background-color: violet;
}

.index_info {
    margin: 0px auto;
}

.acordion_checkbox+.index_info {
    border: black double 0.2rem;
    padding: 0px 1rem;
    background-color: skyblue;
    width: 30rem;
    margin-top: -224px;
    text-align: left;
    text-indent: 1rem;
    clip-path: inset(100% 0 0 0);
    transition: all 0.5s ease-out;
}

.acordion_checkbox:checked+.index_info {
    clip-path: inset(0 0 0 0);
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.inputers {
    margin-top: 0rem;
    display: grid;
    grid-template: "rel txt aws"2rem/6rem 12rem 6rem;
    gap: 0.5rem;
    justify-content: center;
}

.reloll {
    grid-area: rel;
}

.index_textInput {
    grid-area: txt;
}

.answer {
    grid-area: aws;
}

.index_question {
    border: black double 0.2rem;
    padding: 0px 1rem 1.25rem 1rem;
    background-color: skyblue;
    width: 30rem;
    margin: 1rem auto;
    padding-top: 1rem;
    text-indent: 1rem;
    text-align: left;
}

.index_states {
    display: flex;
    justify-content: center;
}

.index_states_common {
    padding: 0px 1rem;
    border: 0.1rem solid black;
    margin:0rem 1rem;
    background-color: #F5F5DC;
    transition: all 0.5s;
}
#states_num{
    transition: all 0.2s;
}

.index_result{
    margin:0px auto;
    width:30rem;
    background-color: whitesmoke;
    border:0.1rem solid black;
    padding:1rem 1rem;
    margin-top:1rem;
}